.fccs-label {
	font-size: 11px;
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;
    display: inline-block;
    margin-bottom: calc(8px);
    padding: 0px;
}

input#react-select-2-input:active, input#react-select-2-input:focus {
    box-shadow: none !important;
}

.fccs-product-selection-description {
    margin-bottom: 0px;
    font-size: 12px;
    font-style: normal;
    color: rgb(117, 117, 117);

    &--manual {
        margin-top: 12px !important;
    }
}